import React from "react";
import { useForm } from "react-hook-form";
import styled from "styled-components";
import useChageUserInfo from "../../common/hooks/useChageUserInfo";
import { useUserInfo } from "../../common/hooks/useUserInfo";

const UserInfoManagement = ({ className }) => {
  // const { name = "Tên đăng nhập", fullname = "Tên của bạn", birth = "" } = data || {};
  const { mutate: chageUserInfo } = useChageUserInfo();
  const { register, handleSubmit, errors, reset } = useForm();
  const { data } = useUserInfo({
    onSuccess: ({ name, fullname, birth }) => reset({ name, fullname, birth }),
  });
  const handle = (data) => {
    chageUserInfo(data);
  };
  return (
    <section className={`${className} user__management__info col-12 col-md-10`}>
      <header className="fs__title fw-bolder">Thông tin cá nhân</header>
      <div className="avatar mt-4 justify-content-center d-flex d-md-none">
        <img src="https://www.sohoceravietnam.com/public/img/meow.jpg" alt="avatar" />
      </div>
      <div className="form mt-4">
        <div className="d-flex flex-column flex-md-row">
          <label htmlFor="" className="fs__text fw-bolder col-12 col-md-6 pe-1">
            Tên Đăng nhập
            <input type="text" name="name" defaultValue={data?.name} disabled />
          </label>
          <label htmlFor="" className="fs__text fw-bolder col-12 col-md-6 ps-1">
            Tên
            <input type="text" name="fullname" defaultValue={data?.fullname} disabled />
          </label>
        </div>
        <div className="d-flex flex-column flex-md-row">
          <label htmlFor="" className="fs__text fw-bolder col-12 col-md-6 pe-1">
            Ngày Tháng Năm Sinh
            <input type="date" name="birth" defaultValue={data?.birth} disabled />
          </label>
          <label htmlFor="" className="fs__text fw-bolder col-md-6 ps-1">
            Số Điện Thoại
            <input type="tel" name="phone" ref={register()} />
          </label>
        </div>
        <div className="d-flex flex-column flex-md-row">
          <label htmlFor="" className="fs__text fw-bolder col-12 col-md-6 pe-1">
            Email
            <input type="email" name="email" ref={register()} />
          </label>
          <label htmlFor="" className="fs__text fw-bolder col-12 col-md-6 ps-1">
            Địa chỉ
            <input type="text" name="address" ref={register()} />
          </label>
        </div>

        <input type="submit" value="Sửa thông tin" onClick={handleSubmit(handle)} />
      </div>
    </section>
  );
};

export default styled(UserInfoManagement)`
  &.user__management {
    &__info {
      /* width: 50%; */
      .avatar {
        width: 100%;
        img {
          /* $size: 100px; */
          width: 200px;
          height: 200px;
          border-radius: 50%;
        }
      }
      .form {
        label {
          display: flex;
          flex-direction: column;
        }
        input:not([type="submit"]) {
          margin: 10px 0;
          background-color: rgba(255, 255, 255, 0);
          border: 1px solid ${(props) => props.theme.colorSecondary};
          padding: 10px 10px 10px 0;
          text-indent: 10px;
          border-radius: 8px;
          outline: none;
          color: ${(props) => props.theme.colorSecondary};
          &::placeholder {
            color: ${(props) => props.theme.colorSecondary};
          }
          &:focus {
            border-width: 2px;
          }
        }
        input[type="submit"] {
          padding: 10px;
          background-color: ${(props) => props.theme.colorSecondary};
          border: 0;
          color: #fafafa;
          border-radius: 8px;
          font-weight: bold;
          font-size: 16px;
          margin: 10px 0;
        }
        input[type="date"] {
          text-indent: 5px;
          &::-webkit-calendar-picker-indicator {
            cursor: pointer;
            filter: invert(1);
            margin-right: 10px;
          }
        }
      }
    }
  }
`;
