import { useMutation, useQueryClient } from "react-query";
import useAxios from "./useAxios";
import { config } from "../../config/api";

const useChageUserInfo = () => {
  const axios = useAxios();
  const queryClient = useQueryClient();
  const query = useMutation(
    async (mutationData) => {
      const { data } = await axios.post(`${config.apiGateWay}/dashboard/user`, mutationData);
      return data.code;
    },
    {
      // onSuccess: (data) => {
      //   // console.log(data);
      //   queryClient.setQueryData(["agencyCodes"], (prev) => {
      //     return { ...prev, data: [data, ...prev.data] };
      //   });
      // },
      onMutate: async (data) => {
        await queryClient.cancelQueries("todos");

        const previousValue = queryClient.getQueryData("userInfo");

        queryClient.setQueryData("userInfo", (old) => ({
          ...old,
          ...data,
        }));

        return previousValue;
      },
      // On failure, roll back to the previous value
      onError: (_err, _variables, previousValue) =>
        queryClient.setQueryData("userInfo", previousValue),
      onSettled: () => {
        queryClient.invalidateQueries(["user", "userInfo"]);
      },
    }
  );
  return query;
};

export default useChageUserInfo;
